import { CalcApplication } from './modules/application/calcApplication';
import { ConvertApplication } from './modules/application/convertApplication';
import { VariableApplication } from './modules/application/variableApplication';
import './../css/style.css';
var variable = new VariableApplication();
function submitCalc() {
    var convert = new ConvertApplication();
    var val = document.getElementById('formulaInput');
    convert.setFormula(val.value);
    var rpn = convert.convertRpn();
    rpn = variable.convertVariable(rpn);
    var calc = new CalcApplication();
    calc.setRpnArr(rpn);
    var result = calc.calc();
    val.value = result;
}
document.getElementById('formulaForm').addEventListener('submit', function (e) {
    e.preventDefault();
    submitCalc();
});
